import ProfileCard from "./Profilecard";
import { Select } from "antd";
import { DatePicker } from "antd";
import { Input } from "antd";


function Employement () {
    return (
        <div>

            <ProfileCard title="Employment Profile" subtitle="Employed Experience" tagline="Please enter the information for the Employed Experience. If not employed before, leave it blank.">
                 <div className="flex w-full flex justify-between mt-4">
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">Occupation</label>
                        <Input placeholder="Select a Test" />
                    </div>
                    <div className="w-[24%]">
                        <label className="text-xs font-bold">Satart Date</label>
                        <DatePicker placeholder="Select Date" />
                    </div>
                    <div className="w-[24%]">
                        <label className="text-xs font-bold">End Date</label>
                        <DatePicker placeholder="Select Date" />
                    </div>
                 </div>
                 <div className="w-full mt-4 flex justify-between">
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">Company</label>
                        <Input placeholder="Fill Company Name" />
                    </div>
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">Type of Employment</label>
                        <Select className="rounded-3xl" placeholder="Select a Employment Type">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                    </div>
                 </div>
                 <div className="w-full mt-4 flex justify-between">
                    
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">Field of Experience</label>
                        <Select className="rounded-3xl" placeholder="Field of Experience">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                    </div>
                 </div>
                 <div className="mt-4">
                            <button className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                        </div>
            </ProfileCard>

        </div>
    )
}

export default Employement;