import ProfileCard from "./Profilecard";
import { Select } from "antd";
import { DatePicker } from "antd";
import { Input } from "antd";


function Personal () {
    return (
        <div>

            <ProfileCard title="Personal Profile" subtitle="Personal Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
                 <div className="flex w-full flex justify-between mt-4">
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">First Name</label>
                        <Input placeholder="Select a Test" value="John" />
                    </div>
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">Last Name</label>
                        <Input placeholder="Select a Test" value="Doe" />
                    </div>
                    
                 </div>
                 <div className="w-[49%] mt-4 flex justify-between">
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">Date of Birth</label>
                        <DatePicker placeholder="Select Date of Birth" />
                    </div>
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">Gender</label>
                        <Select className="rounded-3xl" placeholder="Select Gender">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                    </div>
                 </div>
                 <div className="w-full mt-4 flex justify-between">
                    
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">Country of Birth</label>
                        <Select className="rounded-3xl" placeholder="Select Country">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                    </div>
                    <div className="w-[24%]">
                        <label className="text-xs font-bold">Nationality</label>
                        <Select className="rounded-3xl" placeholder="Select Nationality">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                    </div>
                    <div className="w-[24%]">
                        <label className="text-xs font-bold">Dual Citizenship / Nationality</label>
                        <Select className="rounded-3xl" placeholder="Select Nationality">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                    </div>
                 </div>
                 <div className="w-full mt-4 flex justify-between">
                    
                    
                    <div className="w-[24%]">
                        <label className="text-xs font-bold">Marital Status</label>
                        <Select className="rounded-3xl" placeholder="Select Marital Status">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                    </div>
                 </div>
                
            </ProfileCard>

            <ProfileCard subtitle="Contact Details" tagline="Please enter the information as mentioned in your passport. The same information will be used while applying.">
                 <div className="flex w-full flex justify-between mt-4">
                    <div className="w-[49%]">
                        <label className="text-xs font-bold">First Name</label>
                        <Input placeholder="Select a Test" value="John" />
                    </div>
                    <div className="w-[49%] flex justify-between flex-wrap">
                     

                        <div className="w-[49%]">
                        <label className="text-xs font-bold">State</label>
                        <Select className="rounded-3xl" placeholder="Select a State">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                        </div>

                        <div className="w-[49%]">
                        <label className="text-xs font-bold">City</label>
                        <Select className="rounded-3xl" placeholder="Select City">
                            <Select.Option>Hello</Select.Option>
                        </Select>

                        </div>
                        <div className="w-[49%]">
                        <label className="text-xs font-bold">Postal Code</label>
                        <Input placeholder="Select a Test" value="50000" />
                        </div>

                    </div>
                    
                 </div>
                 
                 
                
            </ProfileCard>

            <ProfileCard subtitle="Study Preference Details" tagline="Please enter the details as per your study search preference.">
                 <div className="flex w-full flex justify-between mt-4">
    
                    <div className="w-full flex justify-between flex-wrap">
                     

                        <div className="w-[49%]">
                        <label className="text-xs font-bold">Education Country</label>
                        <Select className="rounded-3xl" placeholder="India">
                            <Select.Option>Hello</Select.Option>
                        </Select>
                        </div>

                        <div className="w-[49%]">
                        <label className="text-xs font-bold">Preferred Study Destination</label>
                        <Select className="rounded-3xl" placeholder="Australia">
                            <Select.Option>Hello</Select.Option>
                        </Select>

                        </div>
                        

                        <div className="w-[49%]">
                        <label className="text-xs font-bold">Preferred Area of Study</label>
                        <Select className="rounded-3xl" placeholder="Agriculture">
                            <Select.Option>Hello</Select.Option>
                        </Select>

                        </div>
                        

                        <div className="w-[49%]">
                        <label className="text-xs font-bold">Agriculture</label>
                        <Select className="rounded-3xl" placeholder="Bachelor">
                            <Select.Option>Hello</Select.Option>
                        </Select>

                        </div>
                        

                    </div>
                    
                 </div>
            </ProfileCard>

            <div className="mt-4">
                            <button className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                        </div>

        </div>
    )
}

export default Personal;