import ProfileCard from "./Profilecard";
import { Select } from "antd";
import { DatePicker } from "antd";

function Englishtest () {
    return (
        <div>
            <ProfileCard title="English Test Profile" subtitle="English Language Proficiency Test" tagline="Please enter the information for the Graduated English Language Proficiency Test Result.">
                <div className="w-[70%] mt-6 flex justify-between items-center">
                    <div className="left w-[49%]">
                        <div className="pform-container">
                            <label className="text-xs font-bold">Test Name</label>
                            <Select className="rounded-3xl" placeholder="Select A Test">
                                <Select.Option>Hello</Select.Option>
                            </Select>
                        </div>
                    </div>
                    <div className="w-[24%]">
                    <label className="text-xs font-bold">Date of Test</label>
                        <DatePicker placeholder="Test Date" />
                    </div>
                 </div>
                    <div className="w-full flex justify-between my-4">
                            <div className="w-[24%]">
                               <label className="text-xs font-bold">Speaking</label>
                               <Select className="rounded-3xl" placeholder="Select a Score">
                                  <Select.Option>Hello</Select.Option>
                               </Select>
                            </div>
                            <div className="w-[24%]">
                              <label className="text-xs font-bold">Listening</label>
                               <Select className="rounded-3xl" placeholder="Select a Score">
                                 <Select.Option>Hello</Select.Option>
                               </Select>
                            </div>
                            <div className="w-[24%]">
                              <label className="text-xs font-bold">Writing</label>
                               <Select className="rounded-3xl" placeholder="Select a Score">
                                 <Select.Option>Hello</Select.Option>
                               </Select>
                            </div>
                            <div className="w-[24%]">
                              <label className="text-xs font-bold">Reading</label>
                               <Select className="rounded-3xl" placeholder="Select a Score">
                                 <Select.Option>Hello</Select.Option>
                               </Select>
                            </div>
                        </div>
                        <div className="w-full">
                        <div className="w-[24%]">
                              <label className="text-xs font-bold">Overall</label>
                               <Select className="rounded-3xl" placeholder="Select a Score">
                                 <Select.Option>Hello</Select.Option>
                               </Select>
                            </div>
                        </div>
                        <div className="mt-4">
                            <button className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                        </div>
                
            </ProfileCard>

            <div className="mt-8">
                <button className="w-full bg-[#E2E2E2] border border-[#707070] rounded-3xl font-semibold py-2"><span>Add</span></button>
            </div>
        </div>
    )
}

export default Englishtest;