import Footer from "../Footer";
import { useState } from "react";
import Englishtest from "./Englishtest";
import Admissiontest from "./Admissiontest";
import Education from "./Education";
import Employement from "./Enployement";
import Personal from "./Personal";

function Profile () {

    const [profileTabState, setProfileTabState] = useState(1)

    return (
        <div>
             <div
              className="login-footer 
               w-full
               ">
               <div 
                className="top 
                justify-between
                flex 
                justify-between 
                w-[full] 
                mx-auto 
              ">

                <div
                  className="sidebar transition-all duration-500 pr-6 w-[20%] bg-[#DBF9FB] py-8 px-4">
                    <div 
                     className="sidebar-inner
                     rounded-xl">
                        
                    
                     

                     <div className="w-full flex flex-col">
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B]" id="pro_conpleted_btn"
                         onClick={() => setProfileTabState(1)}
                         
                        ><img src="/images/pbtn.png" className="absolute w-[30px] top-[3px] left-[3px]" /> Personal</button>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B]"
                         onClick={() => setProfileTabState(2)}
                         id={profileTabState === 2 ? 'active-tab-btn' : ''}
                         >Education</button>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B]"
                         onClick={() => setProfileTabState(3)}
                         id={profileTabState === 3 ? 'active-tab-btn' : ''}
                         >English Test</button>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B]"
                         onClick={() => setProfileTabState(4)}
                         id={profileTabState === 4 ? 'active-tab-btn' : ''}
                         >Admission Tests</button>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B]"
                         onClick={() => setProfileTabState(5)}
                         id={profileTabState === 5 ? 'active-tab-btn' : ''}
                         >Employment</button>
                        <button className="
                        my-1 w-full text-sm text-[#7C878E] border-[#DBF9FB] 
                        relative border font-semibold rounded-3xl
                         hover:bg-[#FFFCFC] 
                         py-2 hover:border 
                         hover:border-[#0A1C2B]"
                         onClick={() => setProfileTabState(6)}
                         id={profileTabState === 6 ? 'active-tab-btn' : ''}
                         >Employment</button>
                     </div>
                   
                    
                  </div>
                  
                  </div>
            
                 
              <div className="h-[auto] w-[80%] pl-6 py-8 pr-[150px]">
               
               


                <div className="hidden" id={profileTabState === 1 ? 'active-tab-content' : ''}>
                    <Personal />
                </div>

                <div className="hidden" id={profileTabState === 2 ? 'active-tab-content' : ''}>
                    <Education />
                </div>

                <div className="hidden" id={profileTabState === 3 ? 'active-tab-content' : ''}>
                    <Englishtest />
                </div>

                <div className="hidden" id={profileTabState === 4 ? 'active-tab-content' : ''}>
                    <Admissiontest />
                </div>

                <div className="hidden" id={profileTabState === 5 ? 'active-tab-content' : ''}>
                    <Employement />
                </div>

                <div className="hidden" id={profileTabState === 6 ? 'active-tab-content' : ''}>
                    Document
                </div>

                 

              </div>




                
               
            </div>

            
            <Footer />
          
       
       </div>

        </div>
    )
}

export default Profile;