 import ProfileCard from "./Profilecard";
 import { Select } from "antd";
 import { DatePicker } from "antd";
 import { Input } from "antd";


 function Education () {
    return (
        <div>
             <ProfileCard title="English Test Profile" subtitle="English Language Proficiency Test" tagline="Please enter the information for the Graduated English Language Proficiency Test Result.">
                <div className="w-[70%] mt-6 flex justify-between items-center">
                    <div className="left w-[49%]">
                        <div className="pform-container">
                            <label className="text-xs font-bold">Level of Education</label>
                            <Select className="rounded-3xl" placeholder="Select the Level of Education">
                                <Select.Option>Hello</Select.Option>
                            </Select>
                        </div>
                    </div>
                    <div className="w-[24%]">
                    
                    </div>
                 </div>
                    <div className="w-full flex justify-between my-4">
                            <div className="w-[49%]">
                               <label className="text-xs font-bold">Degree Name</label>
                               <Input placeholder="Fill Degree Name" />
                            </div>
                            <div className="w-[49%]">
                              <label className="text-xs font-bold">Fill Degree Name</label>
                              <Input placeholder="Name of Institution" />
                            </div>
                        </div>
                        <div className="w-full flex justify-between">
                        <div className="w-[24%]">
                              
                              <label className="text-xs font-bold">Attended from</label>
                              <DatePicker placeholder="Select Date" />
                            </div>
                            <div className="w-[24%]">
                              
                              <label className="text-xs font-bold">Attended till</label>
                              <DatePicker placeholder="Select Date" />
                            </div>
                            <div className="w-[49%]">
                              
                              <label className="text-xs font-bold">Primary Language of Instruction</label>
                              <Select className="rounded-3xl" placeholder="English">
                                <Select.Option>Hello</Select.Option>
                            </Select>
                            </div>
                        </div>
                        <div className="w-full flex justify-between my-4">
                            <div className="w-[49%]">
                                <div className="w-1/2 flex justify-between mb-2">
                                  <label className="text-xs font-bold">Overall Score</label>
                                  <span className="text-xs font-bold"><input type='checkbox' className="custon_checkbox" /> Overall Score</span>
                                  <span className="text-xs font-bold"><input type='checkbox' className="custon_checkbox" /> Grade</span>
                                </div>
                               <Input placeholder="100%" />
                            </div>
                            <div className="w-[49%]">
                              <label className="text-xs font-bold">Country of Institution</label>
                              <Select className="rounded-3xl" placeholder="Select a Country">
                                <Select.Option>Hello</Select.Option>
                            </Select>
                            </div>
                        </div>
                        <div className="mt-4">
                            <button className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl">Submit</button>
                        </div>
                
            </ProfileCard>
        </div>
    )
 }


 export default Education;